.App {
  text-align: center;
  padding-top:30px;
}

.App-title {
  font-size: 50px;
  font-weight:100;
  color: #EAD7D8;
  padding-bottom:10px;
}

.todos-writingSpace {
  height:35px;
  width:500px;
  border:solid 1px #E6E6E6;
  border-width: 1px 1px 1px 0;
  font-style: italic;
  outline:none; 
  padding:0;
}

.todos-writingSpace::-webkit-input-placeholder {
  color:#EEEEEE;
  font-size:20px;
}

.todos-writingSpace:-ms-input-placeholder {
  color:#EEEEEE;
  font-size:20px;
}

.todos-writingSpace::-ms-input-placeholder {
  color:#EEEEEE;
  font-size:20px;
}

.todos-writingSpace::placeholder {
  color:#EEEEEE;
  font-size:20px;
}

.button{
  height:35px;
  width:50px;
  color:#E6E6E6;
  background: white;
  border: solid 1px #E6E6E6; 
  border-width: 1px 0 1px 1px; 
  font-size:20px;
  font-weight: bold;
  padding:0;
  -webkit-box-sizing:content-box;
          box-sizing:content-box;
  outline:none;
}

.todo {
  height:35px;
  width:550px;
  border:solid 1px #EDEDED;
  margin:auto;
  list-style: none;
}

.fieldSet {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.blackButton {
  color:black;
}

.items-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: left;
      justify-content: left;
  padding-left: 50px;
  font-size:20px;
}

div.done {
  color:#D9D9D9;
  text-decoration:line-through;
}

.checkbox {
  position:absolute;
  left: 9px;
  top: 5px;
  width: 25px;
  height: 20px;
}

.check-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  font-size: 22px;
}

.check-container input {
  position: absolute;
  opacity: 0;
}

.check-container input:checked ~ .checkmark {
  border: 1px solid#D9E9E6;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.check-container input:checked ~ .checkmark:after {
  display: block;
}

.check-container .checkmark:after {
  top: 1px;
  left: 1px;
  width: 18px;
  height: 18px;
  background-image: url("./image/tick.png");
  border-radius:50%;
}

.checkmark{
  position: absolute;
  left: 10px;
  top:5px;
  height: 22px;
  width: 22px;
  border: 1px solid #EDEDED;
  border-radius: 50%;
}

.itemCounter-section {
  position: relative;
  height:35px;
  width:550px;
  border: 1px solid #EDEDED;
  margin:auto;
  padding-top: 10px
}

.itemCounter-element {
  padding:5px;
  color:#B7B7B7;
  position:absolute;
}

.items-left {
  left:0;
}

.clear{
  right:0;
 }

.all {
 left: 150px;
}

.active {
  left: 200px;
}

.completed {
  left: 270px;
}

.active:hover,
.completed:hover,
.all:hover {
  border: 1px solid #F6E8E8;
  border-radius: 5px;
}

.active,
.completed,
.all {
  border: 1px solid white;
}

.itemCounter-element.selected {
  border: 1px solid #EFD6D6;
  border-radius: 5px;
}




